import React from 'react'
import { Link, NavLink } from 'react-router'
import { CartProvider, useCart } from "react-use-cart";

function Navbar() {
  const {totalItems} = useCart();
  return (
    <header className="header-area header-sticky">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <nav className="main-nav">
              {/* <!-- ***** Logo Start ***** --> */}
              <a href="/" className="logo">
                <h1>Villa</h1>
              </a>
              {/* <!-- ***** Logo End ***** --> */}
              {/* <!-- ***** Menu Start ***** --> */}
              <ul className="nav">
                <li><NavLink to="/">Home</NavLink></li>
                <li><NavLink to="/properties">Properties</NavLink></li>
                <li><NavLink to="/details">Property Details</NavLink></li>
                <li><NavLink to="/contact">Contact Us</NavLink></li>
                <li><NavLink to="/product">Product</NavLink></li>
                <li><NavLink to="/shop">Change Villa</NavLink></li>
                <li><Link to="/cart"><i class="fa-solid fa-cart-shopping"><span className='totalItems ms-1'>{totalItems}</span></i> Cart Page</Link></li>
              </ul>
              <a className='menu-trigger'>
                <span>Menu</span>
              </a>
              {/* <!-- ***** Menu End ***** --> */}
            </nav>
          </div>
        </div>
      </div>
    </header>
  )
}

export default Navbar