import React from 'react'
import { CartProvider, useCart } from "react-use-cart";
import { Container, Row } from 'reactstrap';
import Navbar from '../Navbar';
import SubNavbar from '../SubNavbar';
import { Link } from 'react-router';

function Cart() {
  const { isEmpty, totalUniqueItems, items, updateItemQuantity, removeItem, emptyCart, totalItems, cartTotal } = useCart();

  if (isEmpty) return (
    <>
      <h2 className="text-center mt-5 mb-1">Your cart is empty</h2>
      <a href='/shop' className='btn btn-primary d-block mx-auto w-25'>Go Change Villa</a>
    </>
  )

  return (
    <div className="section properties">
      <h1 className="text-center">ALL VILLAS ({totalItems})</h1>
      <a href='/shop' className='btn btn-primary d-block mx-auto w-25'>Go Change Villa</a>

      <button onClick={() => emptyCart()} className="btn btn-danger d-block w-50 mx-auto mb-5 mt-3">Empty Cart</button>

      <Container>
        <Row className="properties-box">
          {items.map((item) => (
            <div key={item.id} className="col-lg-4 col-md-6 align-self-center mb-30 properties-items col-md-6 adv">
              <div className="item">
                <a href="property-details.html"><img src={item.images[0]} alt /></a>
                <span className="category">{item.title}</span>
                <h6>${item.price * item.quantity}</h6>
                <h4><a href="property-details.html">{item.description}</a></h4>
                <ul>
                  <li>Bedrooms: <span>{item.brand}</span></li>
                  <li>Bathrooms: <span>{item.routing}</span></li>
                  <li>Area: <span>{item.stock}</span></li>
                  <li>Floor: <span>{item.category}</span></li>
                  <li>Parking: <span>{item.discountPercentage}</span></li>
                </ul>
                <div className="main-button">
                  <p className="text-center">{item.quantity}</p>
                  <button type="button" className="btn btn-warning" onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) - 1)}>-</button>
                  <button type="button" className="btn btn-success mx-4" onClick={() => updateItemQuantity(item.id, (item.quantity ?? 0) + 1)}>+</button>
                  <button type="button" className="btn btn-danger" onClick={() => removeItem(item.id)}>&times;</button>
                </div>
              </div>

            </div>
          ))}
        </Row>
        <h2 className="text-center mb-5 my-4 ">TOTAL PRICE : <span className="text-success">${cartTotal}</span></h2>
      </Container>
    </div>
  );
}

function Allcart() {
  return (
    <CartProvider>
      <SubNavbar />
      <Navbar />
      <Cart />
    </CartProvider>
  )
}

export default Allcart;