import React, { Component } from 'react'
import { CartProvider } from 'react-use-cart'
import SubNavbar from '../SubNavbar'
import { render } from '@testing-library/react'
import Navbar from '../Navbar'
import axios from 'axios'
import { Container, Row } from 'reactstrap'

class Allproduct extends Component {

  state = {
    products: []
  }
  componentDidMount() {
    axios.get('/assets/js/products.json')
      .then((element) => {
        this.setState(
          {
            products: element.data.products
          }
        )
      })
  }
  render() {
    return (
      <CartProvider>
        <SubNavbar />
        <Navbar />
        <div className='section properties'>
          <Container>
            <Row className='properties-box'>
              {this.state.products.map((result) => (
                <div className="col-lg-4 col-md-6 align-self-center mb-30 properties-items col-md-6 adv">
                  <div className="item">
                    <a href="property-details.html"><img src={result.images[0]} style={{height:'300px'}} alt /></a>
                    <span className="category">{result.title}</span>
                    <h6>${result.price}</h6>
                    <h4><a href="property-details.html">{result.description}</a></h4>
                    <ul>
                      <li>Bedrooms: <span>{result.brand}</span></li>
                      <li>Bathrooms: <span>{result.rating}</span></li>
                      <li>Area: <span>{result.stock}</span></li>
                      <li>Floor: <span>{result.category}</span></li>
                      <li>Parking: <span>{result.discountPercentage}</span></li>
                    </ul>
                    <div className="main-button">
                      <a href="/property">Schedule a visit</a>
                    </div>
                  </div>
                </div>

              ))}
            </Row>
          </Container>
        </div>
      </CartProvider>
    )
  }
}

export default Allproduct