import { CartProvider, useCart } from "react-use-cart";
import Navbar from "../Navbar";
import SubNavbar from "../SubNavbar";
import { Container, Row } from "reactstrap";
import DataApi from "./DataApi";

function Page() {
  const { addItem } = useCart();

  return (

    <div className="section properties">
      <Container>
        <Row className="properties-box">
          {DataApi.map((p) => (
            <div key={p.id} className="col-lg-4 col-md-6 align-self-center mb-30 properties-items col-md-6 adv">
              <div className="item">
                <a href="property-details.html"><img src={p.images[0]} alt /></a>
                <span className="category">{p.title}</span>
                <h6>${p.price}</h6>
                <h4><a href="property-details.html">{p.description}</a></h4>
                <ul>
                  <li>Bedrooms: <span>{p.brand}</span></li>
                  <li>Bathrooms: <span>{p.routing}</span></li>
                  <li>Area: <span>{p.stock}</span></li>
                  <li>Floor: <span>{p.category}</span></li>
                  <li>Parking: <span>{p.discountPercentage}</span></li>
                </ul>
                <div className="main-button">
                  <a type="button" onClick={() => addItem(p)} className="">Add to cart</a>
                </div>
              </div>
            </div>
          ))}
        </Row>
      </Container>
    </div>
  );
}



function Allshoping() {
  return (
    <CartProvider>
      <SubNavbar />
      <Navbar />
      <Page />
    </CartProvider>
  );
}

export default Allshoping;